.section{
        padding-bottom: 40px !important;
        padding-top: 40px !important;
}

.section_title{
        position: relative;
        display: inline-block;
        padding: 0 3px;
        margin-bottom: 25px !important;
}
.MuiTimelineItem-missingOppositeContent::before{
        display: none;
        padding-left: 20px;
}
.child_timeline{
        padding-left: 15px !important;
}


.section_title .section_title_text{
        font-size: 18px;
        font-weight: 600;
        position: relative;
        margin: 0;
}

.section_title span{
        position: absolute;
        height: 5px;
        width: 100%;
        bottom: 0;
        opacity: 0.4;
        left: 0;
        background-color: var(--main-color);
}

.section_content{
        color: rgb(98, 129, 129) !important;
        font: small;
        line-height: 24px !important;
        white-space:pre-line;
}

.MuiTimelineDot-defaultGrey {
        border-radius: 2px solid rgb(5, 4, 4) !important;
        background-color: #ffffff!important;
    }

.titleline_title{
        color: rgb(104, 100, 100) !important;
        font-size: 14px !important;
        font-weight: 600 !important;
        font-style: italic;

}
.titleline_date{
        color: rgb(221, 174, 174)!important;
        font-weight: 400 !important;
        font-style: italic;
}
.titleline_description{
        color: rgb(98, 129, 129) !important;
        font-size: small !important;
        margin-top: 6px !important;
       
}

.timeline .timeline_dot {
        color: rgb(230, 0, 0);
        border-color: #eab70a;
        font-size: small !important;
        padding: 2px !important;
    }


/* service css */

.service{
        height: 100%;
        text-align: center;
}

.service .service_title{
        margin-bottom: 10px !important;
        font-size: 16px !important;
        font-weight: 700 !important;
        line-height: 20px !important;
        text-align: center;
}
.service .service_description{
        color: rgb(98, 129, 129) !important;
        border-color: var(--main-color) !important;
        margin-right: 5px !important;
        text-align: justify;
}
.service  .MuiSvgIcon-root{
        font-size: 40px !important;
        font-weight: 300 !important;
        color: rgb(49, 15, 34)
}
/* end service */

/* skills css */
.skill{
        height: 300px !important;
        padding:40px!important; 
        border: 1px solid rgb(255, 249, 249) !important;
        box-shadow: 50px 55px 92px 0px rgba(0,0,0,0.13) !important;
}

.skill_content{
        display: flex;
        justify-content: center;
        align-items: center;
}
/* .skill_content .MuiGrid-root{
        display: flex;
} */
.skill_description{
        display: flex;
        align-items: center;
}
.timeline_dot_padding{
        margin-right: 10px !important;
        background-color: var(--main-color);
}
