    } */

    .MuiTimelineDot-outlinedGrey {
        box-shadow: none;
        border-color: #aca7a7 !important;
        background-color: var(--main-color)!important;
    }


    /* contact form css */
    .contact_form{
            padding-top: 50px;
            padding-bottom: 50px;
    }

    .contact_info_title{
            font-weight: 600 !important;
            font-size: 15px !important;
    }

.contact_form_social_icon{
        margin-top: 20px !important;
        
}

.contact_icon_wrapper{
        display: flex !important;
        justify-content: center !important;
        align-items: center;
}

.contact_icon_link{
        color: rgb(12, 184, 12);
}

.button_contact_form:hover{
        background-color: #fff;
}

